













































@import "kernel";

footer {
    position: relative;
    z-index: 3;
    background: $color-bg-dark;
    box-shadow: inset 0 1px 0 #000;
    color: $color-inv-text-primary;

    a {
        color: $color-inv-text-primary;
        text-decoration: none;

        &:hover, &:visited, &:active {
            text-decoration: underline;
            color: $color-inv-text-primary;
        }
    }

    .content {
        box-sizing: border-box;
        padding: 40px 0;
    }

    & > header {
        background: $color-bg-dark-alter;

        .content {
            display: grid;
            grid-template-columns: 48% 48%;
            grid-column-gap: 4%;
            justify-items: stretch;
        }

        aside {
            display: flex;
            flex-direction: column;
            box-sizing: border-box;

            a {
                color: #fff;
                font-size: 27px;
                line-height: 30px;
                margin-bottom: 10px;
            }

            code {
                padding: 10px 12px;
                box-sizing: content-box;
                background: rgba(#fff, .1);
                border-radius: 3px;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                color: $color-inv-text-placeholder;
            }
        }

        article {

        }
    }

    & > article {
        .copy {
            display: block;
            text-align: center;
        }
    }
}



